import { FETCH_ITEMS, FETCH_LISTS_SUCCESS, FETCH_ITEMS_SUCCESS, DISMISS_ITEMS } from "../actions/types";

export default function itemReducer(state = {list: null, items: [], showModal: false, loading: false}, action){
    switch (action.type) {
      case FETCH_ITEMS:
        return {
          ...state,
          loading: true,
          showModal: false,
        }
      case FETCH_ITEMS_SUCCESS:
        return {
          ...state,
          loading: false,
          items: action.payload,
          showModal: action.showModal
        }

      case FETCH_LISTS_SUCCESS:
        return{
          ...state,
          loading: false,
          list: action.payload,
        }

      default:
        return state
    }
}
