import React, { Component } from 'react';
import queryString from 'query-string';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import { performItemPost, deleteItem, fetchItems, fetchList, likeItem } from './actions/itemActions';
import { connect } from 'react-redux';
import { ITEMS_URL, LISTS_URL, GUEST_KEY } from './listrUrls';
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles';

import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';

import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';

import Button from '@material-ui/core/Button';
import Collapse from '@material-ui/core/Collapse';
import Chip from '@material-ui/core/Chip';

import Icon from '@material-ui/core/Icon';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import FavoriteIcon from '@material-ui/icons/Favorite';

import CircularProgress from '@material-ui/core/CircularProgress';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';


const styles = theme => ({
  card: {
    display: 'flex',
    flexDirection: 'column',
    height: '100%',
  },
  actions: {
    display: 'flex',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 300,
  },
  expand: {
    transform: 'rotate(0deg)',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
    marginLeft: 'auto',
    [theme.breakpoints.up('sm')]: {
      marginRight: -8,
    },
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.danger,
  },
  warningBtn: {
    fontSize: theme.typography.pxToRem(15),
    color: "#ff1744",
  },
  layout: {
    width: 'auto',
    display: 'block', // Fix IE11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: '100%',
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    textAlign: 'center',
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
  },
  form: {
    width: '100%', // Fix IE11 issue.
    marginTop: theme.spacing.unit,
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
  },
});

class Items extends Component {
  constructor(props){
    super(props);
    this.state = {
      detailsRetrieved: false,
      removeDialog: false,
      unfollowDialog: false,
      itemTitle: '',
      itemDescription: '',
      currentCollab: '',
      loadedOnce: false,
      expanded: false,
      detailDialog: false,
      anchorEl: null,
      collabs: [],
      collabsDialog: false,
      username: null,
      url: null,
      snackOpen: false
    }
  }
  componentWillMount(){
    const values = queryString.parse(this.props.location.search);
    const link = values.l;
    this.props.fetchList(LISTS_URL, link, GUEST_KEY);
    this.props.fetchItems(ITEMS_URL, link, GUEST_KEY);
  }

  expandPanel = panel => (event, expanded) => {
    this.setState({
      expanded: expanded ? panel : false,
    });
  };

  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value});
  }

  addItem(e){
    e.preventDefault();
    const values = queryString.parse(this.props.location.search);
    const link = values.l;
    const itemData = {
      "name": this.state.itemTitle,
      "static_id": this.makeid(),
    }
    if(this.state.itemDescription){
      itemData.description = this.state.itemDescription;
    }
    this.props.performItemPost(ITEMS_URL, itemData, link, GUEST_KEY);
    this.setState({
      itemTitle: null,
      itemDescription: null,
    });
    document.getElementById('itemTitle').value = ''
    document.getElementById('itemDescription').value = ''
  }

  likeItem(e, item, listID){
    e.preventDefault();
    console.log(listID);
    const values = queryString.parse(this.props.location.search);
    const link = values.l;
    const itemData = {"liked_guests": item.liked_guests + 1, "static_id": item.static_id};
    this.props.likeItem(ITEMS_URL, itemData, link, GUEST_KEY);
  }

  makeid() {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (var i = 0; i < 8; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    return text;
  }

  deleteItem(e, itemID){
    e.preventDefault();
    const values = queryString.parse(this.props.location.search);
    const link = values.l;
    this.props.deleteItem(ITEMS_URL, itemID, link, GUEST_KEY);
  }

  render() {
    const { classes } = this.props;

    var items;
    var listDetail;
    if(this.props.listDetail){
      console.log(this.props.listDetail);
      listDetail = (
        <Typography>
          <header>
            <h1>{this.props.listDetail.name}</h1>
          </header>
        </Typography>
      );
    }else if(this.props.listDetail == undefined){
      listDetail = listDetail;
    }
    if(this.props.items){
      items = <CircularProgress />;
      if(this.props.items.length > 0){
        items = this.props.items.map(item => {
            var liked = {marginRight: '10px'};
            var color = "default";
            if(item.description !== ""){
              const target = "#"+item.static_id;
              const showNewLine = {
                whiteSpace: "pre-wrap",
                flexGrow: 1
              }
              if(item.liked_users.includes(this.props.username)){
                color = "primary";
              }
              return (
                <ExpansionPanel key={item.static_id} expanded={this.state.expanded === item.static_id} onChange={this.expandPanel(item.static_id)}>
                  <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                    <Typography className={classes.heading}>{item.name}</Typography>
                  </ExpansionPanelSummary>
                  <ExpansionPanelDetails>
                    <Typography style={showNewLine}>
                      {item.description}
                      <hr />
                      <br />
                      <Chip
                        onClick={(e) => this.likeItem(e, item, this.props.listDetail.static_id)}
                        color={color}
                        icon={<FavoriteIcon />}
                        label={item.liked_users.length + item.liked_guests}
                        style={liked}
                      />
                      <Button className={classes.warningBtn} onClick={(e) => this.deleteItem(e, item.static_id)}>Remove</Button>
                    </Typography>
                  </ExpansionPanelDetails>
                </ExpansionPanel>
            );
          }else{
            if(item.liked_users.includes(this.props.username)){
              color = "primary";
            }
            return (
              <ExpansionPanel key={item.static_id} expanded={this.state.expanded === item.static_id} onChange={this.expandPanel(item.static_id)}>
                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                  <Typography className={classes.heading}>{item.name}</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                  <Chip
                    onClick={(e) => this.likeItem(e, item, this.props.listDetail.static_id)}
                    color={color}
                    icon={<FavoriteIcon />}
                    label={item.liked_users.length + item.liked_guests}
                    style={liked}
                  />
                  <Button className={classes.warningBtn} onClick={(e) => this.deleteItem(e, item.static_id)}>Remove</Button>
                </ExpansionPanelDetails>
              </ExpansionPanel>
            );
          }
        });
      }else{
        items = (
          <ListItem button>
            <ListItemText primary="This List Has No Items" />
          </ListItem>);
      }
    }
    return (
      <div>
      <CssBaseline />
        <Grid item xs={12}>
          <Grid container spacing={8} justify="center" alignItems="center">
            <Grid item xs={12} md={6} style={{marginLeft: 20, marginTop: 40}}>
              {listDetail}
            </Grid>
            <Grid item xs={12} md={7}>
              <Paper className={classes.paper} justify="center" alignItems="center">
                <form autoComplete="off">
                  <TextField
                    id="itemTitle"
                    label="Item Name"
                    className={classes.textField}
                    value={this.state.itemTitle}
                    onChange={(e) => this.handleChange(e)}
                    margin="normal"
                    variant="outlined"
                  />
                  <TextField
                    id="itemDescription"
                    label="Item Description (Optional)"
                    multiline
                    className={classes.textField}
                    value={this.state.itemDescription}
                    onChange={(e) => this.handleChange(e)}
                    margin="normal"
                    variant="outlined"
                  />
                  <Button
                    variant="raised"
                    color="primary"
                    style={{marginTop: 20, marginBottom: 20, width:150, height:45}}
                    onClick={(e) => this.addItem(e)}
                    >
                    Add
                  </Button>
                </form>
              </Paper>

              {items}
            </Grid>
            <Grid item xs={12} md={7} style={{marginTop: 20}}>
            </Grid>
            <Grid item xs={12} md={12}>
            </Grid>

          </Grid>
        </Grid>
      </div>
    );
  }
}


const mapStateToProps = state => ({
  loading: state.items.loading,
  items: state.items.items,
  listDetail: state.items.list
});

List.proptypes = {
  performItemPost: PropTypes.func.isRequired,
  deleteItem: PropTypes.func.isRequired,
  deleteList: PropTypes.func.isRequired,
  likeItem: PropTypes.func.isRequired,
  fetchItems: PropTypes.func.isRequired,
}
var ToExport = withStyles(styles)(Items);
export default connect(mapStateToProps, { performItemPost, deleteItem, fetchItems, fetchList, likeItem })(ToExport);
